import React from 'react';
import PropTypes from 'prop-types';
import { TextContainer, Text, SubTitle } from 'src/theme/styledComponents';
import aboutCoachingImg from 'src/images/about-coaching.png';
import styled from 'styled-components';

const Img = styled.img`
	padding: 40px;
	width: 50vh;
	:last-of-type {
		display: none;
	}
	transition: width 0.3s;
	@media (max-width: 500px) {
		width: 80%;
	}
`;

const Important = styled.span`
	text-transform: uppercase;
	font-weight: bold;
	color: ${props => props.theme.colors.yellow};
`;

const CoachingDetail = ({ title, texts, important }) => (
	<>
		<SubTitle className="sub-title">{title}</SubTitle>
		<Img src={aboutCoachingImg} alt="About Coaching" />
		<TextContainer className="detail-content">
			{texts.map((text, index) => (
				<Text key={index} lineHeight="40px">
					{important && <Important>{important}</Important>}
					{text}
				</Text>
			))}
		</TextContainer>
	</>
);

CoachingDetail.propTypes = {
	title: PropTypes.string,
	texts: PropTypes.arrayOf(PropTypes.string),
	important: PropTypes.string,
};

export default CoachingDetail;
